.carousel-control-prev-icon,
.carousel-control-next-icon {
  height: 100px;
  width: 100px;
  outline: black;
  background-size: 100%, 100%;
  background-image: none;
}

.carousel-control-next-icon:after
{
  content: '>';
  font-size: 35px;
  color: black;
  margin-left: -70px;
}

.carousel-control-prev-icon:after {
  content: '<';
  font-size: 35px;
  color: black;
  margin-left: -50px;
}

.back :hover{
    background-color: white;
}





.center-block {
    display: block;
    margin-right: auto;
    margin-left: auto;
}

.text-medium p{
    font-size: 20px;
    color: #3FA3E7;
}

.btn-magenda{
    color: #fff;
    background-color: #436081;
}
@media only screen and (min-device-width: 300px) and (max-device-width:350px){
    .col-sm-3{
        margin-left: 20%;
    }
    .carousel-control-prev-icon{
        margin-left: 80%;
    }
    .carousel-control-next-icon{
        margin-left: 320%;
    }
    .options{
        width: 28rem;
        height:13rem;
    }
  }
  
  @media only screen and (min-device-width: 360px) and (max-device-width:420px){
    .col-sm-3{
        margin-left: 20%;
    }

    .carousel-control-prev-icon{
        margin-left: 50%;
    }
    .carousel-control-next-icon{
        margin-left: 90%;
    }
    .options{
        width: 28rem;
        height:13rem;
    }
  }
  @media only screen and (min-device-width: 420px) and (max-device-width:540px){
    .col-sm-3{
        margin-left: 28%;
    }
    .options{
        width: 28rem;
        height:13rem;
    }
  }
  @media only screen and (min-device-width: 1020px) and (max-device-width:1100px){
    .carousel-control-next-icon{
        margin-left: 80%;
    }
    .options{
        width: 28rem;
        height:13rem;
    }
  }

